import React, { useEffect, useState } from "react";
import Navbar from "./navbar/Navbar";
import Skills from "./pages/skills/Skills";
import About from "./pages/about/About";
import Projects from "./pages/projects/Projects";
import Contact from "./pages/contact/Contact";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./main.css";
import Home from "./pages/home/Home";

function Main() {
    const [bgToggle, setBgToggle] = useState(true);
    const [langToggle, setLangToggle] = useState(true);

    const [deviceHeight, setDeviceHeight] = useState(window.innerHeight);
    useEffect(() => {
        window.addEventListener("resize", () =>
            setDeviceHeight(window.innerHeight)
        );

        return () => {
            window.removeEventListener("resize", () =>
                setDeviceHeight(window.innerHeight)
            );
        };
    }, []);
    return (
        <div
            style={{ height: `${deviceHeight}px` }}
            className={bgToggle ? "body" : "body-dark"}
        >
            <Router>
                <Navbar
                    bgToggle={bgToggle}
                    setBgToggle={setBgToggle}
                    langToggle={langToggle}
                    setLangToggle={setLangToggle}
                />
                <Switch>
                    <Route
                        path="/"
                        exact
                        render={(props) => (
                            <Home
                                langToggle={langToggle}
                                setLangToggle={setLangToggle}
                                bgToggle={bgToggle}
                                setBgToggle={setBgToggle}
                            />
                        )}
                    />

                    <Route path="/skills" render={(props) => <Skills />} />

                    <Route path="/projects" render={(props) => <Projects />} />

                    <Route path="/contact" render={(props) => <Contact />} />

                    <Route path="./about" render={(props) => <About />} />
                </Switch>
            </Router>
        </div>
    );
}

export default Main;
