import React from "react";
import "./card.css";

import Data from "../../data/Data-skills";

const Card = () => {
    return (
        <>
        
            {Data.map((props) => {
                return (
                    <div key={props.id} className="card-container">
                        <div className="card">
                            <div className="content">
                                <h2>{props.title}</h2>
                                <p>{props.content}</p>
                            </div>
                            <img src={props.img} alt="" />
                        </div>
                    </div>
                );
            })}
        </>
    );
};

export default Card;
