import React from "react";
import { Link } from "react-router-dom";
import { EN, FA } from "../../data/Data-lang";
import "./navbar.css";
import "./neon.css";
import "../toggle-btn/toggle-btn.css";
import { FaRegMoon } from "react-icons/fa";
import { BiSun } from "react-icons/bi";


import Dropdown from "../dropdown/Dropdown";
import ToggleBtn from "../toggle-btn/ToggleBtn";
function Navbar(props) {
    return (
        <>
            <div className="nav">
                <div className="navbar">
                    <div className="navbar-logo">
                        <Link to="/">
                            <img src="assets/logo.svg" alt="F logo" />
                            <div className="aez">
                                <p>
                                    <span>a</span>
                                    <span>e</span>
                                    <span>z</span>
                                </p>
                            </div>
                        </Link>
                    </div>

                    <div
                        className={
                            props.bgToggle ? "nav-links" : "nav-links-dark"
                        }
                    >
                        <ul
                            dir={props.langToggle ? EN.direction : FA.direction}
                        >
                            <li>
                                <Link to="/" className="nav-link ">
                                    {props.langToggle ? EN.home : FA.home}
                                </Link>
                            </li>
                            <Link to="/projects" className="nav-link">
                                <li>
                                    {props.langToggle
                                        ? EN.projects
                                        : FA.projects}
                                </li>
                            </Link>
                            <li>
                                <Link to="/contact" className="nav-link">
                                    {props.langToggle ? EN.contact : FA.contact}
                                </Link>
                            </li>
                            <li>
                                <Link to="/about" className="nav-link">
                                    {props.langToggle ? EN.about : FA.about}
                                </Link>
                            </li>
                            <li>
                                <Link to="/skills" className="nav-link">
                                    {props.langToggle ? EN.skills : FA.skills}
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
                <Dropdown>
                    <ToggleBtn
                        id="lang-toggle"
                        onClick={() => props.setLangToggle(!props.langToggle)}
                        on="EN"
                        off="FA"
                    />
                    <ToggleBtn
                        id="bg-toggle"
                        onClick={() => props.setBgToggle(!props.bgToggle)}
                        on={<BiSun />}
                        off={<FaRegMoon />}
                    />
                </Dropdown>
            </div>
        </>
    );
}

export default Navbar;
