import React from "react";
import "./home.css";
import Fade from "react-reveal/Fade";
import { intro_EN, intro_FA } from "../../../data/Data-lang";
import Button from "../../button/Button";

function Home(props) {
    const element = props.langToggle ? intro_EN : intro_FA;

    return (
        <>
            <div className="container" dir={!props.langToggle ? "rtl" : "ltr"}>
                <div className="box">
                    <Fade bottom cascade duration={1300} delay={500}>
                        <h1>{element.intro}</h1>
                    </Fade>
                </div>
            </div>
        </>
    );
}

export default Home;
